import {GetTopicView,ReplyTopic} from '@/api/class-center'
import KsImage from '@/components/ks-components/image/Image.vue'
import KsCollect from '@/components/ks-components/collect/collect.vue'
import KsEmpty from '@/components/ks-components/empty/empty.vue'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'
export default {
	data(){
		return{
			baseUrl:this.$store.state.SRC+"/config/app",
			loaded:false,
			topicData:{},
			topiclist:[],
			getParameters:{
				topicid:0,
				pageindex:1,
				pagesize:10,
			},
			content:'',
			pageCount:0,
			moreStatus:'more',
			isFocus:false,
			bottomHeight:0
		}
	},
	components:{
		KsImage,
		KsEmpty,
		KsCollect,
		UniLoadMore
	},
	onLoad: function(option) {
		this.getParameters.topicid = option.topicid
		uni.showLoading({
		    title: '加载中'
		});
		this.loadPage()
	},
	onReachBottom(e){
		this.getParameters.pageindex++
		this.moreList()
	},
	onShareAppMessage(){
		var that = this
	　　// 设置菜单中的转发按钮触发转发事件时的转发内容
	　　var shareObj = {
	　　　　title: that.topicData.content,
			path: '/pages/class-center/group/topicview?topicid='+that.getParameters.topicid,
	　　　　imageUrl: that.topicData.imgList[0]||this.baseUrl+'/static/images/common/default-img.png', 
	　　　　success: function(res){
	　　　　　　// 转发成功之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:ok'){
	　　　　　　}
	　　　　},
	　　　　fail: function(){
	　　　　　　// 转发失败之后的回调
	　　　　　　if(res.errMsg == 'shareAppMessage:fail cancel'){
	　　　　　　　　// 用户取消转发
	　　　　　　}else if(res.errMsg == 'shareAppMessage:fail'){
	　　　　　　　　// 转发失败，其中 detail message 为详细失败信息
	　　　　　　}
	　　　　}
	　　};
	　　// 返回shareObj
	　　return shareObj;
	},
	methods: {
		setBottomHeight(e){
			this.isFocus = true
			this.bottomHeight = e.detail.height 
		},
		setNoHeight(){
			this.isFocus = false
			this.bottomHeight = 0
		},
		setResult(e){
			if(e==1){
				this.loadPage()
			}
		},
		reply(){
			if(this.topicData.isTourist){
				if(this.content){
					uni.showLoading({
					    title: '正在提交中'
					});
					ReplyTopic({
						content:this.content,
						topicid:this.getParameters.topicid
					}).then(res=>{
						uni.hideLoading();
						if(res.result){
							uni.showToast({
							    title: res.msg,
							    duration: 2000,
								icon:'none'
							});
							this.loadPage()
						}else{
							uni.showToast({
							    title: res.msg,
							    duration: 2000,
								icon:'none'
							});
						}
						this.content = ''
					})
				}else{
					uni.showToast({
					    title: '请输入回复内容',
					    duration: 2000,
						icon:'none'
					});
				}
			}else{
				var that = this
				that.$store.ready(state=>{
					if(state.isLogin == 1){
						if(that.content){
							uni.showLoading({
								title: '正在提交中'
							});
							ReplyTopic({
								content:that.content,
								topicid:that.getParameters.topicid
							}).then(res=>{
								uni.hideLoading();
								if(res.result){
									uni.showToast({
									    title: res.msg,
									    duration: 2000,
										icon:'none'
									});
									that.loadPage()
								}else{
									uni.showToast({
									    title: res.msg,
									    duration: 2000,
										icon:'none'
									});
								}
								that.content = ''
							})
						}else{
							uni.showToast({
								title: '请输入回复内容',
								duration: 2000,
								icon:'none'
							});
						}
					}else{
						uni.showToast({
							title: '您未登录，请先登录',
							duration: 1500,
							icon:'none'
						});
						setTimeout(function () {
						    uni.navigateTo({
						    	url:'/pages/user/login/index'
						    })
						}, 1500);
						that.content = ''
					}
					
				})
			}
			
			
		},
		moreList(){
			if(this.getParameters.pageindex > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
		},
		loadPage(status){
			if(status=='more'){
				GetTopicView(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						this.topiclist = this.topiclist.concat(res.replyList)
					}
					this.loaded = true
				})
			}else{
				this.getParameters.pageindex = 1
				GetTopicView(this.getParameters).then(res=>{
					if(res.result){
						this.topicData = res
						this.pageCount = res.pageCount
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						this.topiclist = res.replyList
					}else{
						this.topiclist = []
						this.pageCount = 0
					}
					this.loaded = true
					uni.hideLoading();
				})
			}
		},
	}
}
